.navbar {
  display : flex;
  justify-content: end;
  background-color: navy;
}
.navbar a{
  text-decoration: none;
}
.navbar h3{
  margin-left: 10px;
  margin-right: 30px;
  color: white;
}
.register-container{
  width: 50%;
  height: 300px;
  background-color: black;
  padding: 5px;
  margin-top: 10px;
  color: white;
  border-radius: 10px;
}
.register-container input{
  outline: 0;
  border: 1px solid rgb(83, 62, 23);
  padding: 5px;
  border-radius: 5px;
}
.register-container button{
  border: 2px solid blue;
  background-color: blue;
  border-radius: 5px;
  padding: 5px;
  color: white;
}
.login-container{
  height: 200px;
  margin-top: 50px;
}
.myprofile-container{
  background-color: aliceblue;
  width: 50%;
  background-color: black;
  margin: auto;
  margin-top: 100px;
  color: white;
  border-radius: 20px;
}
.myprofile-container button{
  padding: 5px;
  margin-bottom: 10px;
  background-color: blue;
  color: white;
  border: 2px solid blue;
  border-radius: 5px;
}
.myprofile-container h3{
  padding: 10px;
}
body{
  background-color: aliceblue;
}